import React from "react"
import { StaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"
import config from '../../data/SiteConfig'

export default function UpcomingEventsImage() {
  return (
  <StaticQuery
  query = {graphql`
   query {
   image1: file(relativePath: { eq: "gallery/1.png" }) {
     childImageSharp {
       fixed (width: 300 height: 388) {
         ...GatsbyImageSharpFixed
         }
         }
         }
         image2: file(relativePath: { eq: "gallery/2.png" }) {
          childImageSharp {
            fixed (width: 300 height: 260) {
              ...GatsbyImageSharpFixed
              }
              }
              }
              image3: file(relativePath: { eq: "gallery/3.png" }) {
                childImageSharp {
                  fixed (width: 300 height: 388) {
                    ...GatsbyImageSharpFixed
                    }
                    }
                    }
         }
        `}
  render={data => (
    <span>
     <Img fixed={data.image1.childImageSharp.fixed} /><br />
    </span>
  )}
  />
  )
  }
  
